import RequisicoesPadrao from "../../requisicoes";

const get = async(tabela) => {
    let instance = new RequisicoesPadrao();
    return instance.get(tabela);
};
const getPorId = async(tabela, id) => {
    let instance = new RequisicoesPadrao();
    return instance.getId(tabela, id);
};
const add = async(tabela, data) => {
    let instance = new RequisicoesPadrao();
    return instance.inserir(data, tabela);
};
const edit = async(tabela, data) => {
    let instance = new RequisicoesPadrao();
    return instance.editar(data, tabela);
};
const editId = async(tabela, id, data) => {
    let instance = new RequisicoesPadrao();
    return instance.editarId(tabela, id, data);
};
const remove = async(id, tabela) => {
    let instance = new RequisicoesPadrao();
    return instance.remover(id, tabela);
};

const functions = { add, edit, remove, get, getPorId, editId };

export default functions;